<template>
    <div>
        <loading :active.sync="isLoading"></loading>
      <div class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
          <p class="h2"></p>
      </div>
      <div class="text-left mt-3 mb-3 pt-2 pb-2">
        <button type="button" class="btn btn-info btn-sm" @click="openModal('new')"></button>
      </div>
      <div id="couponTable">
          <div id=header class="row font-weight-bold py-2 text-center border-bottom">
            <div class="col-2"></div>
            <div class="col"></div>
            <div class="col-3"></div>
            <div class="col"></div>
            <div class="col-2"></div>
          </div>
          <div class="row py-2 text-center border-bottom " v-for="(item, key) in coupons" :key="key">
            <div class="col-2 mt-auto mb-auto">{{ item.title }}</div>
            <div class="col mt-auto mb-auto">{{ item.percent }}</div>
            <div class="col-3 text-left mt-auto mb-auto">{{ item.deadline.datetime }}</div>
            <div class="col mt-auto mb-auto">
              <span v-if="item.enabled" class="text-success"></span>
              <span v-else class="text-muted"></span>
            </div>
            <div class="col-2 mt-auto mb-auto">
              <button type="button" class="btn btn-outline-info btn-sm m-1"
              @click="openModal('edit',item)"></button>
              <button type="button" class="btn btn-outline-danger btn-sm m-1"
              @click="openModal('delete',item)"></button>
            </div>
          </div>
      </div>
      <!-- Modals -->
      <!-- CouponInfo -->
      <div class="modal" id="couponInfo" tabindex="-1" role="dialog">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">
                <span v-if="status ==='new'"></span>
                <span v-else>  {{ tempCoupon.title }} </span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body text-left">
              <div class="row">
                <div class="col-sm-12">
                  <div class="form-group">
                    <label for="productName"></label>
                    <input type="text" id="productName" class="form-control" placeholder=""
                      v-model="tempCoupon.title" required>
                  </div>
                  <div class="form-group">
                    <label for="couponCode"></label>
                    <input type="text" id="couponCode" class="form-control" placeholder=""
                      v-model="tempCoupon.code" required>
                  </div>
                  <div class="form-group">
                    <label for="dueDate"></label>
                    <input type="date" id="dueDate" class="form-control" placeholder=""
                      v-model="due_date" required>
                  </div>
                  <div class="form-group">
                    <label for="dueTime"></label>
                    <input type="time" id="dueTime" step="1" class="form-control" placeholder=""
                      v-model="due_time" required>
                  </div>
                  <div class="form-group">
                    <label for="price"></label>
                    <input
                      id="price"
                      v-model="tempCoupon.percent"
                      type="number"
                      class="form-control"
                      placeholder="" required
                    >
                  </div>
                  <div class="form-group">
                    <div class="form-check">
                      <input id="enabled" v-model="tempCoupon.enabled" class="form-check-input" type="checkbox"> <!--  true  false  01 data  true  false ?  -->
                      <label class="form-check-label" for="enabled"></label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
              <button type="button" class="btn btn-info" @click="updateCoupon">{{ status === 'new' ? '' : '' }}</button>
            </div>
          </div>
        </div>
      </div>
      <!-- End CouponInfo -->
      <!-- DelCoupon -->
      <div class="modal" id="delCoupon" tabindex="-1" role="dialog">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title"></h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <p><strong>{{ tempCoupon.title }}</strong></p>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
              <button type="button" class="btn btn-danger" @click="delCoupon"></button>
            </div>
          </div>
        </div>
      </div>
      <!-- End DelCoupon -->
    </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'Coupons',
  data () {
    return {
      coupons: {},
      tempCoupon: {
        title: '',
        enabled: false,
        percent: 100,
        deadline_at: 0,
        code: ''
      },
      due_date: '',
      due_time: '',
      status: '',
      isLoading: false,
      uuid: process.env.VUE_APP_UUID
    }
  },
  created () {
    this.getCoupons()
  },
  methods: {
    openModal (status, item) {
      this.status = status
      switch (status) {
        case 'new':
          this.tempCoupon = {
            enabled: false //  tempCoupon{}  enabled  undefined 
          }
          this.due_date = ''
          this.due_time = ''
          $('#couponInfo').modal('show')
          break
        //  const  let  case  {} 
        case 'edit': {
          this.tempCoupon = { ...item }
          //  split 
          const deadlineAt = this.tempCoupon.deadline.datetime.split(' ');
          [this.due_date, this.due_time] = deadlineAt // 
          $('#couponInfo').modal('show')
          break
        }
        case 'delete':
          this.tempCoupon = { ...item }
          $('#delCoupon').modal('show')
          break
        default:
          break
      }
    },
    getCoupons () {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}api/${this.uuid}/admin/ec/coupons`
      this.$http.get(url).then((response) => {
        this.coupons = response.data.data
        this.isLoading = false
      })
    },
    updateCoupon () {
      this.isLoading = true
      let api = `${process.env.VUE_APP_APIPATH}api/${this.uuid}/admin/ec/coupon`
      let httpMethod = ''
      let status = ''
      if (this.status === 'new') {
        httpMethod = 'post'
      } else {
        api = `${process.env.VUE_APP_APIPATH}api/${this.uuid}/admin/ec/coupon/${this.tempCoupon.id}`
        status = ''
        httpMethod = 'patch'
      }
      // 
      //  Y-m-d H:i:s2020-06-16 09:31:18
      this.tempCoupon.deadline_at = `${this.due_date} ${this.due_time}`
      this.$http[httpMethod](api, this.tempCoupon).then(() => {
        $('#couponInfo').modal('hide')
        this.$bus.$emit('message:push',
          status,
          'success')
        this.getCoupons()
      }).catch(() => {
        this.$bus.$emit('message:push',
          '',
          'danger')
        this.isLoading = false
      })
    },
    delCoupon () {
      this.isLoading = true
      const api = `${process.env.VUE_APP_APIPATH}api/${this.uuid}/admin/ec/coupon/${this.tempCoupon.id}`
      this.$http.delete(api).then(() => {
        $('#delCoupon').modal('hide')
        this.$bus.$emit('message:push',
          '',
          'success')
        this.getCoupons()
        this.isLoading = false
      }).catch(() => {
        this.$bus.$emit('message:push',
          '',
          'danger')
        this.isLoading = false
      })
    }
  }
}
</script>
